























































































import Vue from 'vue';

export default Vue.extend({
  name: 'Score',
  computed: {
    score: {
      get() {
        return this.$store.state.score;
      },
      set(value: number) {
        this.$ga.event({
          eventCategory: 'score',
          eventAction: 'change',
          eventValue: value,
        });
        this.$store.commit('updateScore', value);
      },
    },
  },
});
